    <el-row :gutter="20" class="main-row">
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12" class="browser-col" :class="{ 'mobile-hidden': isMobile && activeView !== 'browser' }">
        <el-card class="browser-card">
          <div slot="header">
            <span>ÊµèËßàÂô®‰ø°ÊÅØ</span>
            <el-button size="mini" style="float: right;" @click="refreshBrowserInfo">
              <i class="el-icon-refresh"></i> Âà∑Êñ∞
            </el-button>
