            <div class="info-item">
              <span class="info-label">ÊµèËßàÂô®ÁâàÊú¨:</span>
              <span class="info-value">{{ browserInfo.version }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">ÂÜÖÊ†∏ÂºïÊìé:</span>
              <span class="info-value">{{ browserInfo.engine }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">User Agent:</span>
              <span class="info-value user-agent">{{ browserInfo.userAgent }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">ËØ≠Ë®ÄËÆæÁΩÆ:</span>
              <span class="info-value">{{ browserInfo.language }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">Êó∂Âå∫:</span>
              <span class="info-value">{{ browserInfo.timezone }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">CookieÂêØÁî®:</span>
              <span class="info-value">{{ browserInfo.cookieEnabled ? 'ÊòØ' : 'Âê¶' }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">Âú®Á∫øÁä∂ÊÄÅ:</span>
              <span class="info-value" :class="{ online: browserInfo.onLine, offline: !browserInfo.onLine }">
                {{ browserInfo.onLine ? 'Âú®Á∫ø' : 'Á¶ªÁ∫ø' }}
              </span>
            </div>
