        </el-card>

        <el-card class="device-card" style="margin-top: 20px;" :class="{ 'mobile-hidden': isMobile && activeView !== 'device' }">
          <div slot="header">
            <span>ËÆæÂ§á‰ø°ÊÅØ</span>
            <el-button size="mini" style="float: right;" @click="refreshDeviceInfo">
              <i class="el-icon-refresh"></i> Âà∑Êñ∞
            </el-button>
          </div>
          
          <div class="info-list">
