            </div>
            <div class="info-item">
              <span class="info-label">Â±èÂπïÂàÜËæ®Áéá:</span>
              <span class="info-value">{{ deviceInfo.screenResolution }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">ÂèØÁî®ÂàÜËæ®Áéá:</span>
              <span class="info-value">{{ deviceInfo.availableResolution }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">È¢úËâ≤Ê∑±Â∫¶:</span>
              <span class="info-value">{{ deviceInfo.colorDepth }}‰Ωç</span>
            </div>
            <div class="info-item">
              <span class="info-label">ÂÉèÁ¥†ÊØî:</span>
              <span class="info-value">{{ deviceInfo.pixelRatio }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">Ëß¶Êë∏ÊîØÊåÅ:</span>
              <span class="info-value">{{ deviceInfo.touchSupport ? 'ÊîØÊåÅ' : '‰∏çÊîØÊåÅ' }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">CPUÊ†∏ÂøÉÊï∞:</span>
              <span class="info-value">{{ deviceInfo.cpuCores }}</span>
            </div>
          </div>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12" class="network-col" :class="{ 'mobile-hidden': isMobile && activeView !== 'network' }">
        <el-card class="network-card">
          <div slot="header">
            <span>ÁΩëÁªú‰ø°ÊÅØ</span>
            <el-button size="mini" style="float: right;" @click="refreshNetworkInfo">
              <i class="el-icon-refresh"></i> Âà∑Êñ∞
            </el-button>
          </div>
          
          <div class="info-list">
            <div class="info-item">
              <span class="info-label">IPÂú∞ÂùÄ:</span>
              <span class="info-value">{{ networkInfo.ip || 'Ëé∑Âèñ‰∏≠...' }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">Âú∞ÁêÜ‰ΩçÁΩÆ:</span>
              <span class="info-value">{{ networkInfo.location || 'Ëé∑Âèñ‰∏≠...' }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">ISPÊèê‰æõÂïÜ:</span>
              <span class="info-value">{{ networkInfo.isp || 'Ëé∑Âèñ‰∏≠...' }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">ËøûÊé•Á±ªÂûã:</span>
              <span class="info-value">{{ networkInfo.connectionType }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">ÁΩëÁªúÈÄüÂ∫¶:</span>
              <span class="info-value">{{ networkInfo.downlink }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">RTTÂª∂Ëøü:</span>
              <span class="info-value">{{ networkInfo.rtt }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">Êï∞ÊçÆËäÇÁúÅ:</span>
              <span class="info-value">{{ networkInfo.saveData ? 'ÂºÄÂêØ' : 'ÂÖ≥Èó≠' }}</span>
            </div>
          </div>
        </el-card>

        <el-card class="performance-card" style="margin-top: 20px;" :class="{ 'mobile-hidden': isMobile && activeView !== 'performance' }">
          <div slot="header">
            <span>ÊÄßËÉΩ‰ø°ÊÅØ</span>
            <el-button size="mini" style="float: right;" @click="refreshPerformanceInfo">
              <i class="el-icon-refresh"></i> Âà∑Êñ∞
            </el-button>
          </div>
          
          <div class="info-list">
            <div class="info-item">
              <span class="info-label">È°µÈù¢Âä†ËΩΩÊó∂Èó¥:</span>
              <span class="info-value">{{ performanceInfo.loadTime }}ms</span>
            </div>
            <div class="info-item">
              <span class="info-label">DOMËß£ÊûêÊó∂Èó¥:</span>
              <span class="info-value">{{ performanceInfo.domParseTime }}ms</span>
            </div>
            <div class="info-item">
              <span class="info-label">ÂÜÖÂ≠ò‰ΩøÁî®:</span>
              <span class="info-value">{{ performanceInfo.memoryUsage }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">Â≠òÂÇ®ÈÖçÈ¢ù:</span>
              <span class="info-value">{{ performanceInfo.storageQuota }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">Â∑≤Áî®Â≠òÂÇ®:</span>
              <span class="info-value">{{ performanceInfo.storageUsage }}</span>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-card class="features-card" style="margin-top: 20px;">
      <div slot="header">
        <span>ÊµèËßàÂô®ÁâπÊÄßÊîØÊåÅ</span>
      </div>
      
      <div class="features-grid">
        <div v-for="feature in browserFeatures" :key="feature.name" class="feature-item">
          <div class="feature-name">{{ feature.name }}</div>
          <div class="feature-status" :class="{ supported: feature.supported, unsupported: !feature.supported }">
            <i :class="feature.supported ? 'el-icon-check' : 'el-icon-close'"></i>
            {{ feature.supported ? 'ÊîØÊåÅ' : '‰∏çÊîØÊåÅ' }}
          </div>
        </div>
      </div>
    </el-card>

    <el-card class="actions-card" style="margin-top: 20px;">
      <div slot="header">
        <span>Êìç‰ΩúÂäüËÉΩ</span>
      </div>
      
      <div class="actions-grid">
        <el-button @click="copyAllInfo">
          <i class="el-icon-copy-document"></i> Â§çÂà∂ÊâÄÊúâ‰ø°ÊÅØ
        </el-button>
        <el-button @click="exportInfo">
          <i class="el-icon-download"></i> ÂØºÂá∫‰ø°ÊÅØ
        </el-button>
        <el-button @click="refreshAllInfo">
          <i class="el-icon-refresh"></i> Âà∑Êñ∞ÊâÄÊúâ‰ø°ÊÅØ
        </el-button>
        <el-button @click="testNetworkSpeed">
          <i class="el-icon-connection"></i> ÊµãËØïÁΩëÈÄü
        </el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'ClientInfoView',
  metaInfo: {
    title: 'ÂÆ¢Êà∑Á´Ø‰ø°ÊÅØÊü•ÁúãÂô® - ÊµèËßàÂô®ËÆæÂ§áÁΩëÁªú‰ø°ÊÅØÊ£ÄÊµã',
    meta: [
      { name: 'description', content: 'Êü•ÁúãÊµèËßàÂô®„ÄÅËÆæÂ§á„ÄÅÁΩëÁªúÁ≠âËØ¶ÁªÜ‰ø°ÊÅØÔºåÊ£ÄÊµãÊµèËßàÂô®ÁâπÊÄßÊîØÊåÅÊÉÖÂÜµÂíåÊÄßËÉΩÊï∞ÊçÆ„ÄÇ' },
      { name: 'keywords', content: 'ÂÆ¢Êà∑Á´Ø‰ø°ÊÅØ,ÊµèËßàÂô®‰ø°ÊÅØ,ËÆæÂ§á‰ø°ÊÅØ,ÁΩëÁªú‰ø°ÊÅØ,Á≥ªÁªüÊ£ÄÊµã,ÊµèËßàÂô®ÁâπÊÄß' }
    ]
  },
  data() {
    return {
      browserInfo: {
        name: '',
        version: '',
        engine: '',
        userAgent: '',
        language: '',
        timezone: '',
        cookieEnabled: false,
        onLine: false
      },
      deviceInfo: {
        os: '',
        deviceType: '',
        screenResolution: '',
        availableResolution: '',
        colorDepth: 0,
        pixelRatio: 0,
        touchSupport: false,
        cpuCores: 0
      },
      networkInfo: {
        ip: '',
        location: '',
        isp: '',
        connectionType: '',
        downlink: '',
        rtt: '',
        saveData: false
      },
      performanceInfo: {
        loadTime: 0,
        domParseTime: 0,
        memoryUsage: '',
        storageQuota: '',
        storageUsage: ''
      },
      browserFeatures: [],
      isMobile: false,
      activeView: 'browser'
    }
  },
  mounted() {
    this.initAllInfo()
    this.checkMobile()
    window.addEventListener('resize', this.checkMobile)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkMobile)
  },
  methods: {
    checkMobile() {
      this.isMobile = window.innerWidth <= 768
    },

    handleViewChange() {
      // ÁßªÂä®Á´ØËßÜÂõæÂàáÊç¢Êó∂ÁöÑÂ§ÑÁêÜ
    },

    initAllInfo() {
      this.refreshBrowserInfo()
      this.refreshDeviceInfo()
      this.refreshNetworkInfo()
      this.refreshPerformanceInfo()
      this.checkBrowserFeatures()
    },

    refreshBrowserInfo() {
      const nav = navigator
      const ua = nav.userAgent
      
      this.browserInfo = {
        name: this.getBrowserName(ua),
        version: this.getBrowserVersion(ua),
        engine: this.getBrowserEngine(ua),
        userAgent: ua,
        language: nav.language || nav.userLanguage,
        timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
        cookieEnabled: nav.cookieEnabled,
        onLine: nav.onLine
      }
    },

    refreshDeviceInfo() {
      const screen = window.screen
      
      this.deviceInfo = {
        os: this.getOperatingSystem(),
        deviceType: this.getDeviceType(),
        screenResolution: `${screen.width} √ó ${screen.height}`,
        availableResolution: `${screen.availWidth} √ó ${screen.availHeight}`,
        colorDepth: screen.colorDepth,
        pixelRatio: window.devicePixelRatio || 1,
        touchSupport: 'ontouchstart' in window || navigator.maxTouchPoints > 0,
        cpuCores: navigator.hardwareConcurrency || 'Êú™Áü•'
      }
    },

    async refreshNetworkInfo() {
      // Ëé∑ÂèñÁΩëÁªúËøûÊé•‰ø°ÊÅØ
      const connection = navigator.connection || navigator.mozConnection || navigator.webkitConnection
      
      this.networkInfo = {
        ip: 'Ëé∑Âèñ‰∏≠...',
        location: 'Ëé∑Âèñ‰∏≠...',
        isp: 'Ëé∑Âèñ‰∏≠...',
        connectionType: connection ? connection.effectiveType : 'Êú™Áü•',
        downlink: connection ? `${connection.downlink} Mbps` : 'Êú™Áü•',
        rtt: connection ? `${connection.rtt} ms` : 'Êú™Áü•',
        saveData: connection ? connection.saveData : false
      }

      // Â∞ùËØïËé∑ÂèñIPÂíåÂú∞ÁêÜ‰ΩçÁΩÆ‰ø°ÊÅØ
      try {
        const response = await fetch('https://api.ipify.org?format=json')
        const data = await response.json()
        this.networkInfo.ip = data.ip
        
        // Ëé∑ÂèñÂú∞ÁêÜ‰ΩçÁΩÆ‰ø°ÊÅØ
        const geoResponse = await fetch(`https://ipapi.co/${data.ip}/json/`)
        const geoData = await geoResponse.json()
        this.networkInfo.location = `${geoData.city}, ${geoData.region}, ${geoData.country_name}`
        this.networkInfo.isp = geoData.org
      } catch (error) {
        this.networkInfo.ip = 'Êó†Ê≥ïËé∑Âèñ'
        this.networkInfo.location = 'Êó†Ê≥ïËé∑Âèñ'
        this.networkInfo.isp = 'Êó†Ê≥ïËé∑Âèñ'
      }
    },

    refreshPerformanceInfo() {
      const performance = window.performance
      const timing = performance.timing
      
      this.performanceInfo = {
        loadTime: timing.loadEventEnd - timing.navigationStart,
        domParseTime: timing.domContentLoadedEventEnd - timing.domLoading,
        memoryUsage: this.getMemoryUsage(),
        storageQuota: 'ËÆ°ÁÆó‰∏≠...',
        storageUsage: 'ËÆ°ÁÆó‰∏≠...'
      }

      // Ëé∑ÂèñÂ≠òÂÇ®‰ø°ÊÅØ
      if ('storage' in navigator && 'estimate' in navigator.storage) {
        navigator.storage.estimate().then(estimate => {
          this.performanceInfo.storageQuota = this.formatBytes(estimate.quota)
          this.performanceInfo.storageUsage = this.formatBytes(estimate.usage)
        })
      } else {
        this.performanceInfo.storageQuota = '‰∏çÊîØÊåÅ'
        this.performanceInfo.storageUsage = '‰∏çÊîØÊåÅ'
      }
    },

    checkBrowserFeatures() {
      this.browserFeatures = [
        { name: 'WebGL', supported: this.checkWebGL() },
        { name: 'WebRTC', supported: this.checkWebRTC() },
        { name: 'Service Worker', supported: 'serviceWorker' in navigator },
        { name: 'Web Workers', supported: typeof Worker !== 'undefined' },
        { name: 'WebSocket', supported: typeof WebSocket !== 'undefined' },
        { name: 'Geolocation', supported: 'geolocation' in navigator },
        { name: 'Local Storage', supported: typeof Storage !== 'undefined' },
        { name: 'Session Storage', supported: typeof sessionStorage !== 'undefined' },
        { name: 'IndexedDB', supported: 'indexedDB' in window },
        { name: 'Web Audio', supported: 'AudioContext' in window || 'webkitAudioContext' in window },
        { name: 'WebAssembly', supported: typeof WebAssembly !== 'undefined' },
        { name: 'Push API', supported: 'PushManager' in window },
        { name: 'Notification API', supported: 'Notification' in window },
        { name: 'Vibration API', supported: 'vibrate' in navigator },
        { name: 'Battery API', supported: 'getBattery' in navigator },
        { name: 'Camera/Microphone', supported: 'mediaDevices' in navigator }
      ]
    },

    getBrowserName(ua) {
      if (ua.includes('Chrome')) return 'Chrome'
      if (ua.includes('Firefox')) return 'Firefox'
      if (ua.includes('Safari') && !ua.includes('Chrome')) return 'Safari'
      if (ua.includes('Edge')) return 'Edge'
      if (ua.includes('Opera')) return 'Opera'
      if (ua.includes('MSIE') || ua.includes('Trident')) return 'Internet Explorer'
      return 'Êú™Áü•ÊµèËßàÂô®'
    },

    getBrowserVersion(ua) {
      const match = ua.match(/(Chrome|Firefox|Safari|Edge|Opera|MSIE|rv:)\/?(\d+\.?\d*)/i)
      return match ? match[2] : 'Êú™Áü•ÁâàÊú¨'
    },

    getBrowserEngine(ua) {
      if (ua.includes('WebKit')) return 'WebKit'
      if (ua.includes('Gecko')) return 'Gecko'
      if (ua.includes('Trident')) return 'Trident'
      if (ua.includes('Presto')) return 'Presto'
      return 'Êú™Áü•ÂºïÊìé'
    },

    getOperatingSystem() {
      const ua = navigator.userAgent
      if (ua.includes('Windows')) return 'Windows'
      if (ua.includes('Mac OS')) return 'macOS'
      if (ua.includes('Linux')) return 'Linux'
      if (ua.includes('Android')) return 'Android'
      if (ua.includes('iOS')) return 'iOS'
      return 'Êú™Áü•Á≥ªÁªü'
    },

    getDeviceType() {
      const ua = navigator.userAgent
      if (/tablet|ipad/i.test(ua)) return 'Âπ≥ÊùøÁîµËÑë'
      if (/mobile|android|iphone/i.test(ua)) return 'ÊâãÊú∫'
      return 'Ê°åÈù¢ÁîµËÑë'
    },

    getMemoryUsage() {
      if ('memory' in performance) {
        const memory = performance.memory
        return `${this.formatBytes(memory.usedJSHeapSize)} / ${this.formatBytes(memory.totalJSHeapSize)}`
      }
      return '‰∏çÊîØÊåÅ'
    },

    checkWebGL() {
      try {
        const canvas = document.createElement('canvas')
        return !!(canvas.getContext('webgl') || canvas.getContext('experimental-webgl'))
      } catch (e) {
        return false
      }
    },

    checkWebRTC() {
      return !!(window.RTCPeerConnection || window.mozRTCPeerConnection || window.webkitRTCPeerConnection)
    },

    formatBytes(bytes) {
      if (bytes === 0) return '0 Bytes'
      const k = 1024
      const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB']
      const i = Math.floor(Math.log(bytes) / Math.log(k))
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    },

    async testNetworkSpeed() {
      this.$message.info('ÁΩëÈÄüÊµãËØïÂäüËÉΩÂºÄÂèë‰∏≠...')
      // ËøôÈáåÂèØ‰ª•ÂÆûÁé∞ÁΩëÈÄüÊµãËØïÂäüËÉΩ
    },

    refreshAllInfo() {
      this.initAllInfo()
      this.$message.success('ÊâÄÊúâ‰ø°ÊÅØÂ∑≤Âà∑Êñ∞')
    },

    async copyAllInfo() {
      const info = this.getAllInfoText()
      try {
        await navigator.clipboard.writeText(info)
        this.$message.success('ÊâÄÊúâ‰ø°ÊÅØÂ∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø')
      } catch (error) {
        this.$message.error('Â§çÂà∂Â§±Ë¥•')
      }
    },

    exportInfo() {
      const data = {
        browserInfo: this.browserInfo,
        deviceInfo: this.deviceInfo,
        networkInfo: this.networkInfo,
        performanceInfo: this.performanceInfo,
        browserFeatures: this.browserFeatures,
        exportTime: new Date().toISOString()
      }

      const content = JSON.stringify(data, null, 2)
      const blob = new Blob([content], { type: 'application/json;charset=utf-8' })
      const url = URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.href = url
      a.download = `ÂÆ¢Êà∑Á´Ø‰ø°ÊÅØ_${Date.now()}.json`
      document.body.appendChild(a)
      a.click()
      document.body.removeChild(a)
      URL.revokeObjectURL(url)
      
      this.$message.success('‰ø°ÊÅØÂØºÂá∫ÊàêÂäü')
    },

    getAllInfoText() {
      return `ÂÆ¢Êà∑Á´Ø‰ø°ÊÅØÊä•Âëä
ÁîüÊàêÊó∂Èó¥: ${new Date().toLocaleString()}

=== ÊµèËßàÂô®‰ø°ÊÅØ ===
ÊµèËßàÂô®ÂêçÁß∞: ${this.browserInfo.name}
ÊµèËßàÂô®ÁâàÊú¨: ${this.browserInfo.version}
ÂÜÖÊ†∏ÂºïÊìé: ${this.browserInfo.engine}
ËØ≠Ë®ÄËÆæÁΩÆ: ${this.browserInfo.language}
Êó∂Âå∫: ${this.browserInfo.timezone}
CookieÂêØÁî®: ${this.browserInfo.cookieEnabled ? 'ÊòØ' : 'Âê¶'}
Âú®Á∫øÁä∂ÊÄÅ: ${this.browserInfo.onLine ? 'Âú®Á∫ø' : 'Á¶ªÁ∫ø'}

=== ËÆæÂ§á‰ø°ÊÅØ ===
Êìç‰ΩúÁ≥ªÁªü: ${this.deviceInfo.os}
ËÆæÂ§áÁ±ªÂûã: ${this.deviceInfo.deviceType}
Â±èÂπïÂàÜËæ®Áéá: ${this.deviceInfo.screenResolution}
